.square {
  border-radius : 10px ;
  width: 30vw;
  height: 30vh;
  margin: 0,auto;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-template-rows : repeat(3,1fr);
}

.box {
  display: flex;
  justify-content: center;
  font-size: 50px;
  font-weight: 800;
  cursor: pointer;
  outline: none;
}

.bord{
  width: 200px;
  margin: 20px , auto;
}